import React, { useState, useRef, useEffect } from 'react';

// Hàm chuyển âm sang tiếng Việt không dấu
const removeVietnameseTones = (str) => {
  return str
    .normalize('NFD')
    .replace(/[\u0300-\u036f]/g, '')
    .replace(/đ/g, 'd')
    .replace(/Đ/g, 'D');
};

const viDuDict = {
  11: {
    1: 'về hưu',
    2: 'con hươu, con khướu',
    3: 'bông hoa',
    4: 'loè loẹt',
    5: 'hoa huệ, sao khuê',
    6: 'hươ tay',
    7: 'huy chương',
    8: 'khuya khoắt',
    9: 'nhồm nhoàm',
    10: 'khù khoằm',
  },
  12: {
    1: 'mắt trái xoan, hạt xoàn',
    2: 'hoạt bát, sột xoạt',
    3: 'tóc xoăn, thoăn thoắt',
    4: 'tóc xoăn, thoăn thoắt',
    5: 'khuân vác, uẩn khúc',
    6: 'quần quật, xuất chúng',
    7: 'hoen ố, nhoẻn cười',
    8: 'loè loẹt, khoét tổ',
    9: 'khuyên tai, dấu huyền',
    10: 'bông tuyết, trăng khuyết',
  },
  13: {
    1: 'màn tuyn, dầu luyn',
    2: 'xe buýt, tuýt còi, huýt sáo, xoắn xuýt',
    3: 'khoang tàu, áo choàng',
    4: 'áo khoác, xoạc chân',
    5: 'con hoẵng, huyênh hoang',
    6: 'dấu ngoặc, hoặc là',
    7: 'mới toanh, oanh liệt, khoanh tay',
    8: 'oạch, thu hoạch',
    9: 'chuếnh choáng, huyênh hoang',
    10: 'bộc tuệch, rỗng tuyếch, trống huếch'
  },
  14: {
    1: 'Phụ huynh, khuynh thành, huỳnh huỵch, mừng quýnh, hoa quỳnh, luýnh quýnh',
    2: 'huỳnh huỵch',
    3: 'loai choai, chủng loại',
    4: 'xoay sở, loay hoay',
    5: 'quây quần ',
    6: 'xoong nồi, boong tàu',
    7: 'đàn oóc, quần soóc, con voọc, xe rơ moóc',
    8: 'đèn tuýp, tuýp thuốc, tuýp đánh răng',
    9: 'ngoằn ngoèo, nghoẻo, nghoẹo cổ',
    10: 'nguều ngoào'
  },
  15: {
    1: 'nguều ngoào',
    1: 'ì oạp, ngoáp, ngoạp',
    3: 'khúc khuỷu, ngã khuỵu, khuỷu tay',
    4: 'bâng khuâng',
  }
}

const VietnameseSyllables = () => {

  const [activeButton, setActiveButton] = useState(null);
  const [audio, setAudio] = useState(null); // Trạng thái lưu Audio object
  const [vidu, setVidu] = useState(''); // Trạng thái lưu Audio object

  
  // Mảng 2 chiều thể hiện chính xác layout trong ảnh
  const syllablesTable = [
    ["am", "ap", "ăm", "ăp", "âm", "âp", "em", "ep", "im", "ip"],
    ["iêm", "yêm", "iêp", "om", "op", "ôm", "ôp", "ơm", "ơp", "um"],
    ["up", "uôm", "ươm", "ươp", "an", "at", "ăn", "ăt", "ân", "ât"],
    ["en", "et", "ên", "êt", "in", "it", "iên", "iêt", "yên", "yêt"],
    ["on", "ot", "ôn", "ôt", "ơn", "ơt", "un", "ut", "ưt", "uôn"],
    ["uôt", "ươn", "ươt", "ang", "ac", "ăng", "ăc", "âng", "âc", "eng"],
    ["ec", "iêng", "yêng", "iêc", "ong", "oc", "ông", "ôc", "ung", "uc"],
    ["ưng", "ưc", "uông", "uôc", "ương", "ươc", "anh", "ach", "ênh", "êch"],
    ["inh", "ich", "ai", "ay", "oi", "ây", "ôi", "ơi", "ui", "ưi"],
    ["uôi", "ươi", "ao", "eo", "au", "âu", "êu", "iu", "iêu", "yêu"],
    ["ưu", "ươu", "oa", "oe", "uê", "ươ", "uy", "uya", "oam", "oăm"],
    ["oan", "oat", "oăn", "oăt", "uân", "uât", "oen", "oet", "uyên", "uyêt"],
    ["uyn", "uyt", "oang", "oac", "oăng", "oăc", "oanh", "oach", "uênh", "uêch"],
    ["uynh", "uych", "oai", "oay", "uây", "oong", "ooc", "uyp", "oeo", "uêu"],
    ["oao", "oap", "uyu", "uâng", "", "", "", "", "", ""]
  ];

  // Xử lý phát âm thanh
  const playSyllable = (rowIndex,colIndex,syllable) => {
    if (!audio) return;

    // if (!syllable) return; // Không làm gì nếu ô trống
    
    // // Phát âm thanh
    // const utterance = new SpeechSynthesisUtterance(syllable);
    // utterance.lang = 'vi-VN';
    
    // Hiệu ứng khi click
    setActiveButton(syllable);
    setVidu(viDuDict[rowIndex] && viDuDict[rowIndex][colIndex]?`Ví dụ: ${viDuDict[rowIndex][colIndex]}`:'');
    // window.speechSynthesis.speak(utterance);

    var filename =  `${rowIndex >= 10 ? rowIndex: '0' + rowIndex}${colIndex<10?colIndex:'a'}${removeVietnameseTones(syllable)}`;
    
    const fileUrl = `https://td.nhidonghocphat.com/static/phatam/${filename}.mp3`;

    // Cập nhật src và play
    audio.src = fileUrl;
    audio.play().catch((err) => {
      console.error('Không thể phát âm thanh:', err);
    });

    // Reset trạng thái active sau 1 giây
    setTimeout(() => {
      setActiveButton(null);
    }, 5000);
  };


  useEffect(() => {
    // Chạy chỉ trên client
    if (typeof window !== 'undefined') {
      const audioInstance = new window.Audio(); // Tránh lỗi SSR
      setAudio(audioInstance);
    }
  }, []);

  // Lấy các class dựa trên trạng thái nút
  const getSyllableButtonClass = (syllable) => {
    if (!syllable) {
      return 'ptav_syllable-button ptav_syllable-button-empty';
    }
    
    if (activeButton === syllable) {
      return 'ptav_syllable-button ptav_syllable-button-active';
    }
    
    return 'ptav_syllable-button ptav_syllable-button-normal';
  };

  return (
    <div className="ptav_container">
      {/* Tiêu đề */}
      <div className="ptav_header">
        <h1 className="ptav_title">BẢNG VẦN TIẾNG VIỆT</h1>
        <span className="ptav_subtitle">Nhấn vào các nút để nghe phát âm</span>
      </div>
      
      {/* Bảng vần */}
      <div className="ptav_table-container">
        <table className="ptav_syllable-table">
          <tbody>
            {syllablesTable.map((row, rowIndex) => (
              <tr key={`row-${rowIndex}`}>
                {row.map((syllable, colIndex) => (
                  <td key={`cell-${rowIndex}-${colIndex}`} className="ptav_syllable-cell">
                    <button
                      onClick={() => syllable && playSyllable(rowIndex+1,colIndex+1, syllable)}
                      className={getSyllableButtonClass(syllable)}
                      disabled={!syllable}
                    >
                      {syllable}
                    </button>
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      
      {/* Thông tin hiển thị vần đang phát */}
      <div className="ptav_status-area">
        {activeButton ? (
          <div className="ptav_flex-col">
            <span className="ptav_active-syllable">{activeButton}</span>
            <span className="ptav_status-text">{vidu}</span>
          </div>
        ) : (
          <p className="ptav_status-placeholder">Chọn một vần để nghe phát âm</p>
        )}
      </div>
      
      {/* Footer */}
      <div className="ptav_footer">
        <p>Học vần tiếng Việt - Tài liệu giảng dạy</p>
        <div className="ptav_footer-icon-container">
          <svg className="ptav_footer-icon" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M5 3v4M3 5h4M6 17v4m-2-2h4m5-16l2.286 6.857L21 12l-5.714 2.143L13 21l-2.286-6.857L5 12l5.714-2.143L13 3z"></path>
          </svg>
          <span>Bản 2025</span>
        </div>
      </div>
    </div>
  );
}

export default VietnameseSyllables;